import { Link } from 'react-router-dom';
import { SHOP_PAGE } from 'config';

export default function Categories() {
    return (
        <div>
            <div className='flex flex--gap'>
                <div className='col-sm-6 col-lg-6'>
                    <Link to={SHOP_PAGE + '?filter=iphone'}>
                        <div className='cover categories'>
                            <img src='./images/products/1.png' alt='Iphone' />
                        </div>
                    </Link>
                </div>
                <div className='col-sm-6 col-lg-6'>
                    <Link to={SHOP_PAGE + '?filter=mac'}>
                        <div className='cover categories'>
                            <img src='./images/products/2.png' alt='MAC' />
                        </div>
                    </Link>
                </div>
                <div className='col-sm-4 col-lg-4'>
                    <Link to={SHOP_PAGE + '?filter=ipad'}>
                        <div className='cover categories'>
                            <img src='./images/products/3.png' alt='IPAD' />
                        </div>
                    </Link>
                </div>
                <div className='col-sm-4 col-lg-4'>
                    <Link to={SHOP_PAGE + '?filter=watch'}>
                        <div className='cover categories'>
                            <img src='./images/products/4.png' alt='WATCH' />
                        </div>
                    </Link>
                </div>
                <div className='col-sm-4 col-lg-4'>
                    <Link to={SHOP_PAGE + '?filter=airpod'}>
                        <div className='cover categories'>
                            <img src='./images/products/5.png' alt='AirPods' />
                        </div>
                    </Link>
                </div>
            </div>
        </div>

    )
}