import React, { useEffect, useState } from "react";
import axios from 'axios';
import { SHOP_PAGE, SERVER_API } from "config";
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';

import Title from 'components/Title';
import ProductsList from 'components/ProductsList';
import Banner from 'UI/Banner';
import Categories from './Categories';
import Services from './Services';

import './style.scss';

export default function HomePage() {
    const client = axios.create({ baseURL: SERVER_API });

    const [products, setProducts] = useState([]);
    const [onload, setOnload] = useState(true);

    const catchError = e => {
        if (e.response && e.response.data) {
            toast.error(e.response.data.errors[0]);
        } else {
            toast.error('Kết nối máy chủ thất bại, vui lòng thử lại');
        }
    }

    const getData = () => {
        client.get('/products').then((rs) => {
            setOnload(false);
            setProducts(s => rs.data.data);
        }).catch(e => catchError(e));
    }
    useEffect(() => getData(), []);

    return (
        <main>
            <section className='clear--padding'>
                <div className='container hero-banner'>
                    <Banner image='url(./images/banner/1.jpg)' height={480}>
                        <p className="uppercase">New Inspiration 2020</p>
                        <h2 className="uppercase">20% off on new season</h2>
                        <div className='gap'></div>
                        <Link className="btn black" to={SHOP_PAGE}>Browse collections</Link>
                    </Banner>
                </div>
            </section>
            <section>
                <div className="container">
                    <div className="gap"></div>
                    <Title
                        title='Browse our categories'
                        subtitle='Carefully created collections'
                        align='center'
                    />
                    <div className='gap'></div>
                    <Categories />
                </div>
            </section>

            <section>
                <div className="container">
                    <Title
                        title='TOP TRENDING PRODUCTS'
                        subtitle='MADE THE HARD WAY'
                        align='left'
                    />
                    <div className='gap'></div>
                    <ProductsList items={products} onload={onload} column={4} preview={true} />
                </div>
            </section>

            <section>
                <div className="container">
                    <Services />
                </div>
            </section>
        </main>
    )
}